.app__context {
   position: absolute;
   background: white;
   width: 220px;
}

.app__context-close {
   position: absolute;
   width: 25px;
   height: 25px;
   background: url('../icons/close-icon.svg') center no-repeat;
   top: 0;
   right: 0;
   cursor: pointer;
   opacity: 0.3;
   z-index: 2;
}

.app__context-close:hover {
   opacity: 1;
}

.context-menu {
   padding-right: 20px;
   color: black
}

.context-menu li {
   background: white;
   padding: 2px;
   margin: 4px 0;
}

.context-menu li:hover {
   background: rgb(207, 207, 207);
   cursor: pointer;
}