.map {
   width: 100%;
   max-width: 100%;
   height: 100%;
   overflow: hidden;
}
.map-body {
   position: relative;
   overflow: hidden;
   width: 100%;
   height: 100%;
}
.map-container {
   position: relative;
   width: 100%;
   height: 100%;
}

.map__resize-button {
   position: absolute;
   padding: 0.6rem;
   color: black;
   background: white;
   font-size: 16px;
   top: 50%;
   left: 20%;
   z-index: 9999999;
}