.app__body {
   background: #0e232e;
   width: 100%;
   min-height: 100%;
}

.main__info {
   margin: 0 auto;
   max-width: 900px;
}

.part__heading h2 {
   color: white;
   font-size: 22px;
   font-weight: 700;
   margin-bottom: 10px;
   padding: 20px 0 10px 0;
   border-bottom: 1px solid rgba(255, 255, 255, 0.397);
}
.part__info {
   color: #adb3b6;
}
.part__info p {
   margin-bottom: 10px;
}
.main-list {
   margin-top: 10px;
   padding: 4px 2px;
}
.main-list li {
   font-size: 18px;
}
.main-list li:not(:last-child) {
   margin-bottom: 14px;
}

span.bg-s {
   font-weight: 700;
   font-size: 20px;
}