.popup {
   display: none;
   position: absolute;
   width: 350px;
   padding: 10px;
   border-radius: 8px;
   top: 50%;
   left: 50%;
   background: white;
   transform: translate(-50%, -50%);
   z-index: 10;
}
.popup.active {
   display: block;
}
.popup__close {
   position: absolute;
   top: 5px;
   right: 5px;
   width: 20px;
   height: 20px;
   background: url('../icons/close-icon.svg') center no-repeat;
   opacity: 0.3;
   cursor: pointer;
}
.popup__close:hover {
   opacity: 1;
}
.popup__body {
   background: white;
   color: black;
   padding: 10px;
}
.popup__body form {
   display: flex;
   flex-direction: column;
   justify-content: center;
}
.popup__body label {
   font-size: 16px;
   margin: 0 3px;
}
.popup__body input {
   background: #f4f4f4;
   border-radius: 4px;
   padding: 5px 2px;
   margin: 10px 0;
   font-size: 18px;
}
.popup__body textarea {
   background: #f4f4f4;
   border-radius: 4px;
   padding: 5px 2px;
   margin: 10px 0;
   font-size: 18px;
   resize: none;
}
.popup__delete {
   padding: 1rem;
}
.popup__delete h2 {
   font-weight: 600;
   margin-bottom: 10px;
}
.popup__buttons {
   display: flex;
   align-items: center;
   justify-content: center;
}
.popup__buttons button {
   padding: 0.3rem 1.5rem;
}
.form-radio {
   display: flex;
   justify-content: space-evenly;
}
.form-radio span {
   margin-left: 4px;
}