.login {
   width: 100%;
   min-height: 100%;
   
}
.login__container {
   margin: 80px auto 0;
   max-width: 400px;
   width: 100%;
   padding: 15px;
   background: white;
   border-radius: 8px;
   -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.8);
   -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.8);
   box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.8);
}
.login__title h2 {
   font-size: 24px;
   font-weight: bold;
   color:black;
   margin-bottom: 20px;
   text-align: center;
}
.login__form {
   display: flex;
   align-items: center;
   justify-content: center;
}

.login-form input {
   font-size: 18px;
   background: #e4e4e4;
   color: black;
   min-height: 40px;
   padding: 4px 8px;
   margin-bottom: 10px;
   border-radius: 5px;
   border: 1px solid #f2f2f2;
}

.login-form-button {
   text-align: center;
}

.logout {
   color: black;
}

.logout__title {
   font-size: 24px;
   font-weight: 900;
   text-align: center;
   margin-top: 2rem;
}

.logout__link {
   display: flex;
   align-items: center;
   justify-content: center;
   color: white !important;
   font-size: 16px;
   padding: 8px 10px;
}