.sidebar {
   position: fixed;
   top: 60px;
   left: 0;
   z-index: 5;
   background: whitesmoke;
   color: black;
   height: 100%;
   max-width: 325px;
   width: 100%;
   padding: 10px 0;
   transform: translateX(-100%);
   transition: transform .3s ease 0s;
}
.map.w-sb .sidebar {
   transform: translateX(0%);
   transition: transform .3s ease 0s;
}
.map.w-sb .sidebar__content {
   display: block;
}
.sidebar__header {
   text-align: center;
   margin-bottom: 30px;
}
.sidebar__filters {
   padding: 0 20px 0 10px;
   border-bottom: 1px solid #f5f5f5;
   transition: all .3s ease 0s;
}
.sidebar__open {
   position: absolute;
   top: 50%;
   right: -45px;
   width: 42px;
   height: 42px;
   background: #f5f5f5;
   cursor: pointer;
   border-radius: 50%;
   z-index: 2;
   transition: all .3s ease 0s;
}
.sidebar__content {
   display: none;
}
.map.w-sb .sidebar__open {
   right: -30px;
}
.sidebar__open svg {
   width: 100%;
   height: 100%;
}
.sidebar__open:hover {
   transform: scale(1.1);
}
.map.w-sb .sidebar__open>svg {
   transform: rotate(-180deg);
} 
.filter__name {
   font-size: 16px;
   position: relative;
   background-color: #fff;
   margin-bottom: 30px;
   border-radius: 8px;
   box-shadow: 0 4px 12px 0 rgb(0 0 0 / 20%);
   border: none;
   outline: none;
}
.filter__name.filter-name-white {
   position: relative;
   height: auto;
   max-height: 150px;
   overflow-x: hidden;
   overflow-y: auto;
   font-size: 16px;
}
.filter__name.filter-name-white .filter-name-search{
   border-radius: 8px;
}
.filter-name-search {
   width: 100%;
   color: #333;
   height: 34px;
   outline: none;
   font-size: 16px;
   border-radius: 8px;
   padding: 8px 30px 8px 8px;
}
.search-icon {
   position: absolute;
   top: 10px;
   right: 10px;
   width: 14px;
   height: 14px;
   cursor: pointer;
}
.filter-type {
   font-size: 16px;
   padding: 4px;
   cursor: pointer;
}

/* Suggest */

.filter__suggests {
   display: block;
   margin-top: 5px;
   width: 100%;
   border-radius: 0 0 8px 8px;
}
.filter__suggests.hidden {
   display: none;
}
.filter-suggests-list {
   
   border-radius: 0 0 8px 8px;
}
.filter-suggests-list li {
   display: block;
   cursor: pointer;
   padding: 4px 10px;
}
.filter-suggests-list li:hover {
   background-color: #f2f2f2;
}
.filter-suggests-list li:hover:last-child {
   border-radius: 0 0 8px 8px;
}