@media (max-width: 940px) {
   .main__info {
      max-width: 650px;
      padding: 0 10px;
   }
}

@media (max-width: 480px) {
   .main__info {
      max-width: 100%;
   }
   .part__heading h2 {
      font-size: 20px;
      text-align: center;
   }
   .login__title h2 {
      font-size: 18px;
   }
   .login__container {
      max-width: 300px;
      padding: 10px;
   }
   .sidebar {
      max-width: 100%;
   }
   .map.w-sb .sidebar__open {
      right: 10px;
   }
}