.button {
   color: white;
   padding: 6px 12px;
   font-size: 18px;
   border-radius: 4px;
   max-width: 200px;
   margin: 10px auto 0;
}
.button-primary {
   background-color: #007bff;
}
.button-success {
   background-color: #28a745;
}
.button-warning {
   background-color: #ffc107;
}
.button-danger {
   background-color: #dc3545;
}
